.main {
  display: flex;
  justify-content: center;
  background-color: rgba(0, 188, 212, 1);
}

.nav-menu_container {
  width: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}

.navbar {
  display: flex;
  background-color: #312f2f;
  padding: 5px 20px;
  border-radius: 15px;
  align-items: center;
  justify-content: center;
}

.option {
  padding: 12px 15px;
  color: grey;
  cursor: pointer;
  text-align: center;
  font-weight: 600;
  display: inline-block;
  width: 150px;
  
  line-height: 40px;
  margin: 0px 5px;
}

.option:hover {
  background-color: grey;
  color: rgb(61, 61, 219);
  border-radius: 10px; 
}

.option.active-option {
  background-color: white;
  color: rgb(61, 61, 219);
  border-radius: 10px;
}

.menu-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.menu {
  display: none;
  background-color: white;
  z-index: 1;
  border-radius: 10px;
  box-sizing: border-box;
  margin-top: 10px;
  width: 100%;
  padding: 15px 10px;
}

.blog {
  padding: 10px; 
}

.title {
  font-weight: 800;
  font-size: 15px;
}

.details {
  color: #888;
  font-size: 15px;
  margin-top: 8px;
  margin-bottom: 10px;
}

.menu.active {
  display: block;
}